.dropdown-menu .ant-divider-horizontal {
  margin: 6px 0px;
}
.dropdown-menu .user-image-container {
  height: 80px !important;
  padding: 10px !important;
  margin: 0 !important;
  display: flex;
  align-items: center;
}
.dropdown-menu .user-image-container a {
  display: flex !important;
  align-items: center;
}
.dropdown-menu .user-image-container .name {
  display: inline-block;
  height: 60px;
  line-height: 60px;
  padding-top: 10px;
}
.dropdown-menu .user-image-container .link {
  display: block;
}
.dropdown-menu .user-image-container .user-image {
  display: inline-block;
  position: relative;
  width: 60px;
  height: 60px;
  overflow: hidden;
  border-radius: 50%;
  margin: 0px auto;
}
.dropdown-menu .user-image-container .user-image img {
  width: auto;
  height: 100%;
}
.dropdown-menu .user-image-container .title {
  display: block;
  margin-left: 16px;
  font-weight: 600;
  line-height: normal;
}
.dropdown-menu .user-image-container .small-text {
  font-size: 12px;
  font-weight: normal;
}
.dropdown-menu .menu-item-container {
  height: 40px !important;
  padding: 5px !important;
  margin: 0 !important;
  display: flex;
  align-items: center;
}
.dropdown-menu .menu-item-container a {
  display: flex !important;
  align-items: center;
}
.dropdown-menu .menu-item-container .link {
  display: block;
}
.dropdown-menu .menu-item-container .item-image {
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.dropdown-menu .menu-item-container .item-title {
  display: block;
  margin-left: 16px;
  font-weight: 500;
  line-height: normal;
}
