.footer {
  z-index: 1;
  background: #fff;
  padding-top: 40px;
}
.footer a {
  color: #000;
}
.footer h3 {
  font-weight: bold;
  color: #000;
}
.footer .footer-item span {
  color: #666666;
}
.footer .license {
  display: none;
}
.footer .all-link {
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .footer-item {
    text-align: center;
    margin-bottom: 20px;
  }
  .footer .address {
    display: none;
  }
  .footer .license {
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #666666;
  }
  .footer .all-link {
    margin: 0;
  }
}
