.login-modal {
  display: flex;
  flex-direction: column;
  width: 460px !important;
}
.login-modal .title-underline {
  width: 30%;
  border: 0;
  border-top: 1px solid #ffc500;
}
.login-modal h2 {
  text-align: center;
  font-weight: 600;
}
.login-modal .ant-modal-body {
  padding: 20px;
}
.login-modal .ant-input {
  border-radius: 8px;
}
.login-modal .login-button {
  width: 100%;
  border-radius: 8px;
  margin-bottom: 10px;
  margin-top: 10px;
}
.login-modal .forgot-password-button {
  width: 100%;
  border-radius: 8px;
  background: #ebebeb;
}
.login-modal .login-facebook-button {
  width: 48%;
  border-radius: 8px;
  margin-bottom: 15px;
  margin-top: 15px;
  float: left;
  background: #3b5998;
  border-color: #3b5998;
}
.login-modal .login-google-button {
  width: 48%;
  border-radius: 8px;
  margin-bottom: 15px;
  margin-top: 15px;
  float: right;
  background: #d44638;
  border-color: #d44638;
}
@media (max-width: 768px) {
  .login-modal {
    width: 100% !important;
  }
}
@media (max-width: 576px) {
  .login-facebook-button {
    font-size: 12px;
  }
  .login-google-button {
    font-size: 12px;
  }
}
