.admin-container .header {
  height: 64px;
  padding: 0;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  position: relative;
}
.admin-container .header .right {
  float: right;
  height: 100%;
  overflow: hidden;
}
.admin-container .header .right .headerSearch :global(.anticon-search) {
  cursor: pointer;
  font-size: 16px;
}
.admin-container .header .right .headerSearch .input {
  transition: width 0.3s, margin-left 0.3s;
  width: 0;
  background: transparent;
  border-radius: 0;
}
.admin-container .header .right .headerSearch .input :global(.ant-select-selection) {
  background: transparent;
}
.admin-container .header .right .headerSearch .input input {
  border: 0;
  padding-left: 0;
  padding-right: 0;
  box-shadow: none !important;
}
.admin-container .header .right .headerSearch .input,
.admin-container .header .right .headerSearch .input:hover,
.admin-container .header .right .headerSearch .input:focus {
  border-bottom: 1px solid #d9d9d9;
}
.admin-container .header .right .headerSearch .input.show {
  width: 210px;
  margin-left: 8px;
}
.admin-container .header .right .search {
  padding: 0 12px;
}
.admin-container .header .right .search:hover {
  background: transparent;
}
.admin-container .content {
  margin: 24px;
}
.admin-container .footer {
  padding: 16px;
  margin: 48px 0 0 0;
  text-align: center;
}
.admin-container .footer .links {
  margin-bottom: 8px;
}
.admin-container .footer .links a {
  color: rgba(0, 0, 0, 0.45);
  transition: all 0.3s;
}
.admin-container .footer .links a:not(:last-child) {
  margin-right: 40px;
}
.admin-container .footer .links a:hover {
  color: rgba(0, 0, 0, 0.65);
}
.admin-container .footer .copyright {
  color: rgba(0, 0, 0, 0.45);
  font-size: 14px;
}
:global .top-nav-menu li.ant-menu-item {
  height: 64px;
  line-height: 64px;
}
:global .drawer .drawer-content {
  background: #001529;
}
:global .ant-menu-inline-collapsed > .ant-menu-item .sider-menu-item-img + span,
:global .ant-menu-inline-collapsed > .ant-menu-item-group > .ant-menu-item-group-list > .ant-menu-item .sider-menu-item-img + span,
:global .ant-menu-inline-collapsed > .ant-menu-submenu > .ant-menu-submenu-title .sider-menu-item-img + span {
  max-width: 0;
  display: inline-block;
  opacity: 0;
}
:global .ant-menu-item .sider-menu-item-img + span,
:global .ant-menu-submenu-title .sider-menu-item-img + span {
  transition: opacity 0.3s ease-in-out, width 0.3s ease-in-out;
  opacity: 1;
}
:global .ant-drawer-left .ant-drawer-body {
  padding: 0;
}
