.left-sidebar {
  display: flex;
  flex-direction: column;
}
.left-sidebar .image-promotion-right {
  margin-top: 30px;
}
.left-sidebar .ant-menu-submenu-title {
  border-bottom: 1px solid #F4F4F4;
}
.left-sidebar .title {
  padding: 15px;
  text-align: center;
  background: #FFC500;
  width: 100%;
  color: #000;
  font-size: 18px;
}
.left-sidebar .all-brand {
  color: #333;
  background: #fff;
  padding: 15px;
  text-align: center;
  border-bottom: 1px solid #F4F4F4;
  border-left: 1px solid #F4F4F4;
  border-right: 1px solid #F4F4F4;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}
.left-sidebar-mobile {
  display: flex;
  flex-direction: column;
}
.left-sidebar-mobile .image-promotion-right {
  margin-top: 30px;
}
.left-sidebar-mobile .ant-menu-submenu-title {
  border-bottom: 1px solid #F4F4F4;
}
.left-sidebar-mobile .title {
  padding: 15px;
  text-align: center;
  background: #FFC500;
  width: 100%;
  color: #fff;
  font-size: 18px;
}
.left-sidebar-mobile .all-brand {
  color: #333;
  background: #fff;
  padding: 15px;
  text-align: center;
  border-bottom: 1px solid #F4F4F4;
  border-left: 1px solid #F4F4F4;
  border-right: 1px solid #F4F4F4;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}
@media (max-width: 992px) {
  .left-sidebar {
    padding: 20px;
    margin-bottom: 20px;
  }
  .left-sidebar .image-promotion-right {
    display: none;
  }
  .left-sidebar-mobile {
    padding: 0px;
  }
  .left-sidebar-mobile .image-promotion-right {
    display: none;
  }
  .left-sidebar-mobile .title {
    margin-top: 20px;
    text-align: left;
  }
  .left-sidebar-mobile .all-brand {
    text-align: left;
    margin-bottom: 20px;
  }
}
@media (max-width: 1120px) {
  .left-sidebar .title {
    font-size: 14px;
  }
}
