.sider {
  min-height: 100vh;
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
  position: relative;
  z-index: 10;
}
.sider .logo {
  height: 64px;
  position: relative;
  line-height: 64px;
  padding-left: 24px;
  transition: all 0.3s;
  background: white;
  overflow: hidden;
  border-bottom: 1px solid #d9d9d9;
}
.sider .logo img {
  display: inline-block;
  vertical-align: middle;
  height: 32px;
}
.sider .logo h1 {
  color: white;
  display: inline-block;
  vertical-align: middle;
  font-size: 20px;
  margin: 0 0 0 12px;
  font-weight: 600;
}
